import "./index.scss";
import { Head } from "~/components/head";
import { axios } from "&";
import { ViewDemo } from "../../components/demo";

export default class ILike extends Component {
  constructor() {
    super();
    this.state = {
      likeList: [],
      pic: require("@/assets/images/touxiang.jpg")
    };
  }
  componentDidMount() {
    axios.post("/react/getILike").then(res => {
      this.setState({
        likeList: res.data.result
      });
    });
  }
  render() {
    const { likeList, pic } = this.state;
    return (
      <div>
        <Head title="我的喜欢" leftShow={true}></Head>
        <div
          style={{
            display: likeList.length == 0 ? "block" : "none",
            fontFamily: "幼圆",
            fontSize: 25,
            margin: "5rem 0 0.4rem",
            textAlign: "center"
          }}
        >
          您的喜欢列表空空如也。。。
        </div>
        <div style={{ display: likeList.length == 0 ? "none" : "block" }}>
          {likeList.map((item, i) => {
            return <ViewDemo key={i} word={item.word} pic={pic}></ViewDemo>;
          })}
        </div>
      </div>
    );
  }
}
